<template>
<div :style='{"border":"1px solid #ddd","padding":"20px","margin":"20px auto 0","borderRadius":"16px","background":"#fff","width":"1200px","position":"relative"}'>
    <el-form
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="80px"
    >
          <el-form-item :style='{"border":"1px solid #dfdfdf","padding":"10px","boxShadow":"1px 2px 3px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"linear-gradient(180deg, rgba(254,244,218,1) 0%, rgba(254,244,218,1) 51%, rgba(255,234,178,1) 51%, rgba(255,234,178,1) 100%)"}' label="甜品编号" prop="yongpinbianhao">
              <el-input v-model="ruleForm.yongpinbianhao" placeholder="甜品编号" readonly></el-input>
          </el-form-item>
          <el-form-item :style='{"border":"1px solid #dfdfdf","padding":"10px","boxShadow":"1px 2px 3px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"linear-gradient(180deg, rgba(254,244,218,1) 0%, rgba(254,244,218,1) 51%, rgba(255,234,178,1) 51%, rgba(255,234,178,1) 100%)"}' label="甜品名称" prop="yongpinmingcheng">
            <el-input v-model="ruleForm.yongpinmingcheng" 
                placeholder="甜品名称" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"border":"1px solid #dfdfdf","padding":"10px","boxShadow":"1px 2px 3px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"linear-gradient(180deg, rgba(254,244,218,1) 0%, rgba(254,244,218,1) 51%, rgba(255,234,178,1) 51%, rgba(255,234,178,1) 100%)"}'  label="甜品类型" prop="yongpinfenlei">
            <el-select v-model="ruleForm.yongpinfenlei" placeholder="请选择甜品类型"  >
              <el-option
                  v-for="(item,index) in yongpinfenleiOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"border":"1px solid #dfdfdf","padding":"10px","boxShadow":"1px 2px 3px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"linear-gradient(180deg, rgba(254,244,218,1) 0%, rgba(254,244,218,1) 51%, rgba(255,234,178,1) 51%, rgba(255,234,178,1) 100%)"}' label="口味" prop="pinpai">
            <el-input v-model="ruleForm.pinpai" 
                placeholder="口味" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"border":"1px solid #dfdfdf","padding":"10px","boxShadow":"1px 2px 3px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"linear-gradient(180deg, rgba(254,244,218,1) 0%, rgba(254,244,218,1) 51%, rgba(255,234,178,1) 51%, rgba(255,234,178,1) 100%)"}' label="甜品图片" v-if="type!='cross' || (type=='cross' && !ro.yongpintupian)" prop="yongpintupian">
            <file-upload
            tip="点击上传甜品图片"
            action="file/upload"
            :limit="3"
            :multiple="true"
            :fileUrls="ruleForm.yongpintupian?ruleForm.yongpintupian:''"
            @change="yongpintupianUploadChange"
            ></file-upload>
          </el-form-item>
            <el-form-item :style='{"border":"1px solid #dfdfdf","padding":"10px","boxShadow":"1px 2px 3px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"linear-gradient(180deg, rgba(254,244,218,1) 0%, rgba(254,244,218,1) 51%, rgba(255,234,178,1) 51%, rgba(255,234,178,1) 100%)"}' class="upload" v-else label="甜品图片" prop="yongpintupian">
                <img v-if="ruleForm.yongpintupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.yongpintupian.split(',')[0]" width="100" height="100">
                <img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.yongpintupian.split(',')" :src="baseUrl+item" width="100" height="100">
            </el-form-item>
          <el-form-item :style='{"border":"1px solid #dfdfdf","padding":"10px","boxShadow":"1px 2px 3px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"linear-gradient(180deg, rgba(254,244,218,1) 0%, rgba(254,244,218,1) 51%, rgba(255,234,178,1) 51%, rgba(255,234,178,1) 100%)"}' label="商户账号" prop="shangjiazhanghao">
            <el-input v-model="ruleForm.shangjiazhanghao" 
                placeholder="商户账号" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"border":"1px solid #dfdfdf","padding":"10px","boxShadow":"1px 2px 3px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"linear-gradient(180deg, rgba(254,244,218,1) 0%, rgba(254,244,218,1) 51%, rgba(255,234,178,1) 51%, rgba(255,234,178,1) 100%)"}' label="店铺名称" prop="dianpumingcheng">
            <el-input v-model="ruleForm.dianpumingcheng" 
                placeholder="店铺名称" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"border":"1px solid #dfdfdf","padding":"10px","boxShadow":"1px 2px 3px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"linear-gradient(180deg, rgba(254,244,218,1) 0%, rgba(254,244,218,1) 51%, rgba(255,234,178,1) 51%, rgba(255,234,178,1) 100%)"}' label="单限" prop="onelimittimes">
            <el-input v-model="ruleForm.onelimittimes" 
                placeholder="单限" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"border":"1px solid #dfdfdf","padding":"10px","boxShadow":"1px 2px 3px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"linear-gradient(180deg, rgba(254,244,218,1) 0%, rgba(254,244,218,1) 51%, rgba(255,234,178,1) 51%, rgba(255,234,178,1) 100%)"}' label="库存" prop="alllimittimes">
            <el-input v-model="ruleForm.alllimittimes" 
                placeholder="库存" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"border":"1px solid #dfdfdf","padding":"10px","boxShadow":"1px 2px 3px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"linear-gradient(180deg, rgba(254,244,218,1) 0%, rgba(254,244,218,1) 51%, rgba(255,234,178,1) 51%, rgba(255,234,178,1) 100%)"}' label="价格" prop="price">
            <el-input v-model="ruleForm.price" 
                placeholder="价格" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"border":"1px solid #dfdfdf","padding":"10px","boxShadow":"1px 2px 3px #eee","margin":"0 0 8px 0","borderRadius":"8px","background":"linear-gradient(180deg, rgba(254,244,218,1) 0%, rgba(254,244,218,1) 51%, rgba(255,234,178,1) 51%, rgba(255,234,178,1) 100%)"}' label="制作方法" prop="shangpinjianjie">
            <editor 
                :style='{"minHeight":"300px","padding":"0","margin":"0","borderColor":"#ccc","backgroundColor":"#f9f9f9","borderRadius":"0","borderWidth":"0","width":"100%","borderStyle":"solid","height":"auto"}'
                v-model="ruleForm.shangpinjianjie" 
                class="editor" 
                action="file/upload">
            </editor>
          </el-form-item>

      <el-form-item :style='{"padding":"0","margin":"20px 0 20px 0"}'>
        <el-button :style='{"border":"0","cursor":"pointer","padding":"0","boxShadow":"1px 2px 8px #ccc","margin":"0 20px 0 0","color":"#fff","outline":"none","borderRadius":"4px","background":"radial-gradient(circle, rgba(43,224,232,1) 0%, rgba(0,173,181,1) 100%)","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}'  type="primary" @click="onSubmit">提交</el-button>
        <el-button :style='{"border":"1px solid #bbb","cursor":"pointer","padding":"0","boxShadow":"1px 2px 6px #ccc","margin":"0","color":"#999","outline":"none","borderRadius":"20px","background":"#fff","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' @click="back()">返回</el-button>
      </el-form-item>
    </el-form>
</div>
</template>

<script>
  export default {
    data() {
      return {
        id: '',
        baseUrl: '',
        ro:{
            yongpinbianhao : false,
            yongpinmingcheng : false,
            yongpinfenlei : false,
            pinpai : false,
            yongpintupian : false,
            shangpinjianjie : false,
            shangjiazhanghao : false,
            dianpumingcheng : false,
            onelimittimes : false,
            alllimittimes : false,
            clicktime : false,
            clicknum : false,
            price : false,
        },
        type: '',
        userTableName: localStorage.getItem('UserTableName'),
        ruleForm: {
          yongpinbianhao: this.getUUID(),
          yongpinmingcheng: '',
          yongpinfenlei: '',
          pinpai: '',
          yongpintupian: '',
          shangpinjianjie: '',
          shangjiazhanghao: '',
          dianpumingcheng: '',
          onelimittimes: '-1',
          alllimittimes: '-1',
          clicktime: '',
          clicknum: '',
          price: '',
        },
        yongpinfenleiOptions: [],
        rules: {
          yongpinbianhao: [
          ],
          yongpinmingcheng: [
          ],
          yongpinfenlei: [
          ],
          pinpai: [
          ],
          yongpintupian: [
          ],
          shangpinjianjie: [
          ],
          shangjiazhanghao: [
          ],
          dianpumingcheng: [
          ],
          onelimittimes: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          alllimittimes: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          clicktime: [
          ],
          clicknum: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          price: [
            { required: true, message: '价格不能为空', trigger: 'blur' },
            { validator: this.$validate.isNumber, trigger: 'blur' },
          ],
        },
      };
    },
    computed: {



    },
    created() {
	  //this.bg();
      let type = this.$route.query.type ? this.$route.query.type : '';
      this.init(type);
      this.baseUrl = this.$config.baseUrl;
    },
    methods: {
      getMakeZero(s) {
          return s < 10 ? '0' + s : s;
      },
      // 下载
      download(file){
        window.open(`${file}`)
      },
      // 初始化
      init(type) {
        this.type = type;
        if(type=='cross'){
          var obj = JSON.parse(localStorage.getItem('crossObj'));
          for (var o in obj){
            if(o=='yongpinbianhao'){
              this.ruleForm.yongpinbianhao = obj[o];
              this.ro.yongpinbianhao = true;
              continue;
            }
            if(o=='yongpinmingcheng'){
              this.ruleForm.yongpinmingcheng = obj[o];
              this.ro.yongpinmingcheng = true;
              continue;
            }
            if(o=='yongpinfenlei'){
              this.ruleForm.yongpinfenlei = obj[o];
              this.ro.yongpinfenlei = true;
              continue;
            }
            if(o=='pinpai'){
              this.ruleForm.pinpai = obj[o];
              this.ro.pinpai = true;
              continue;
            }
            if(o=='yongpintupian'){
              this.ruleForm.yongpintupian = obj[o].split(",")[0];
              this.ro.yongpintupian = true;
              continue;
            }
            if(o=='shangpinjianjie'){
              this.ruleForm.shangpinjianjie = obj[o];
              this.ro.shangpinjianjie = true;
              continue;
            }
            if(o=='shangjiazhanghao'){
              this.ruleForm.shangjiazhanghao = obj[o];
              this.ro.shangjiazhanghao = true;
              continue;
            }
            if(o=='dianpumingcheng'){
              this.ruleForm.dianpumingcheng = obj[o];
              this.ro.dianpumingcheng = true;
              continue;
            }
            if(o=='onelimittimes'){
              this.ruleForm.onelimittimes = obj[o];
              this.ro.onelimittimes = true;
              continue;
            }
            if(o=='alllimittimes'){
              this.ruleForm.alllimittimes = obj[o];
              this.ro.alllimittimes = true;
              continue;
            }
            if(o=='clicktime'){
              this.ruleForm.clicktime = obj[o];
              this.ro.clicktime = true;
              continue;
            }
            if(o=='clicknum'){
              this.ruleForm.clicknum = obj[o];
              this.ro.clicknum = true;
              continue;
            }
            if(o=='price'){
              this.ruleForm.price = obj[o];
              this.ro.price = true;
              continue;
            }
          }
        }
        // 获取用户信息
        this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            var json = res.data.data;
            if((json.shangjiazhanghao!=''&&json.shangjiazhanghao) || json.shangjiazhanghao==0){
                this.ruleForm.shangjiazhanghao = json.shangjiazhanghao
            }
            if((json.dianpumingcheng!=''&&json.dianpumingcheng) || json.dianpumingcheng==0){
                this.ruleForm.dianpumingcheng = json.dianpumingcheng
            }
          }
        });
        this.$http.get('option/yongpinfenlei/yongpinfenlei', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.yongpinfenleiOptions = res.data.data;
          }
        });
      },

    // 多级联动参数
      // 多级联动参数
      info(id) {
        this.$http.get('tiyuyongpin/detail/${id}', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
      },
      // 提交
      onSubmit() {

        //更新跨表属性
        var crossuserid;
        var crossrefid;
        var crossoptnum;
        this.$refs["ruleForm"].validate(valid => {
          if(valid) {
            if(this.type=='cross'){
                 var statusColumnName = localStorage.getItem('statusColumnName');
                 var statusColumnValue = localStorage.getItem('statusColumnValue');
                 if(statusColumnName && statusColumnName!='') {
                     var obj = JSON.parse(localStorage.getItem('crossObj'));
                     if(!statusColumnName.startsWith("[")) {
                         for (var o in obj){
                             if(o==statusColumnName){
                                 obj[o] = statusColumnValue;
                             }
                         }
                         var table = localStorage.getItem('crossTable');
                         this.$http.post(table+'/update', obj).then(res => {});
                     } else {
                            crossuserid=Number(localStorage.getItem('userid'));
                            crossrefid=obj['id'];
                            crossoptnum=localStorage.getItem('statusColumnName');
                            crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                     }
                 }
            }
            if(crossrefid && crossuserid) {
                 this.ruleForm.crossuserid=crossuserid;
                 this.ruleForm.crossrefid=crossrefid;
                 var params = {
                     page: 1,
                     limit: 10,
                     crossuserid:crossuserid,
                     crossrefid:crossrefid,
                 }
                 this.$http.get('tiyuyongpin/list', {
                  params: params
                 }).then(res => {
                     if(res.data.data.total>=crossoptnum) {
                         this.$message({
                          message: localStorage.getItem('tips'),
                          type: 'success',
                          duration: 1500,
                         });
                          return false;
                     } else {
                         // 跨表计算


                          this.$http.post('tiyuyongpin/add', this.ruleForm).then(res => {
                              if (res.data.code == 0) {
                                  this.$message({
                                      message: '操作成功',
                                      type: 'success',
                                      duration: 1500,
                                      onClose: () => {
                                          this.$router.go(-1);
                                      }
                                  });
                              } else {
                                  this.$message({
                                      message: res.data.msg,
                                      type: 'error',
                                      duration: 1500
                                  });
                              }
                          });
                     }
                 });
             } else {


                  this.$http.post('tiyuyongpin/add', this.ruleForm).then(res => {
                     if (res.data.code == 0) {
                          this.$message({
                              message: '操作成功',
                              type: 'success',
                              duration: 1500,
                              onClose: () => {
                                  this.$router.go(-1);
                              }
                          });
                      } else {
                          this.$message({
                              message: res.data.msg,
                              type: 'error',
                              duration: 1500
                          });
                      }
                  });
             }
          }
        });
      },
      // 获取uuid
      getUUID () {
        return new Date().getTime();
      },
      // 返回
      back() {
        this.$router.go(-1);
      },
      yongpintupianUploadChange(fileUrls) {
          this.ruleForm.yongpintupian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");;
      },
    }
  };
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
